section.healthydreamzslideshow{
    width: 100%;
}
section.healthydreamzslideshow div.slide-frame{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
    width: 100vw;
}
section.healthydreamzslideshow section.current-slide{
    width: 100vw;
    height: 350px;
}
section.healthydreamzslideshow section.previous-slide,
section.healthydreamzslideshow section.next-slide{
    width: 20px;
    height: 20px;
}
:hover section.healthydreamzslideshow section.previous-slide,
:hover section.healthydreamzslideshow section.next-slide{
    cursor: pointer;
}
section.healthydreamzslideshow section.slide div.slide-thumb{
    width: 100vw;
    height: 100%;
    background: lightseagreen;
    background-size: cover;
    background-position: center;
}
section.healthydreamzslideshow div.slideshow-controls{
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
section.healthydreamzslideshow div.slideshow-controls span{
    padding: 10px;
    width: 100px;
    border: none;
    background: lightseagreen;
    color: #f5f5f5;
    cursor: pointer;
    text-align: center;
    box-shadow: 3px 3px 5px #551d8283;
    user-select: none;
}
section.healthydreamzslideshow div.healthydreamzslideshow-controller span:hover{
    color: #ffc200;
    text-shadow: 3px 3px 5px #ffc200;
}
section.healthydreamzslideshow div.healthydreamzslideshow-controller span:active{
    color: lightseagreen;
    text-shadow: 3px 3px 5px lightseagreen;
    background: #ffc200;
}