body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
}

button {
  padding: 1px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size:15px;
  background-color: black;
  color: white;
  border-radius: 1px;
  cursor: pointer;
}

.login {
  margin-top: 18px;
  padding: 5px;
  background-color: white ;
  color: lightsteelblue;
  border: none;
  font-family: 'Fontdiner Swanky', cursive;
  font-size: 20px;
  font-weight: bold;
  float: right;
  box-shadow: 1px 2px 2px 1px rgba(0,0,0,0.2);
}

.profile {
  color:white;
}

h3, h4 {
  margin-bottom: 0;
}

h1{
  margin-top:5px;
}

nav {
  border-bottom: 4px solid black;
  position: fixed;
  top: 0;
  width: 100%;
  background: white;
  font-family: 'Fontdiner Swanky', cursive;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
}

.brand {
  padding-top: 20px;
  padding-bottom: 20px;
  margin: auto;
  font-family:'Fontdiner Swanky', cursive ;
  font-size: 26px;
  font-weight: bold;
  color:white;
  text-align: center;
}

.cursive{
  font-family:'Fontdiner Swanky', cursive ;
}

.bold{
  font-weight: bold;
}

nav p{
  float: left;
  height: 100%;
  text-align: center;
  font-weight: bold;
  padding-top: 10px;
  padding-left: 32px;
}

nav a {
  text-decoration: none;
  color: white;
  font-weight: bold;
  padding: 16px 32px;
}

nav a:hover {
  background: black;
  color: white;
}

#page-body {
  width: 100vw;
  padding-top: 75px;
}

.article-list-item {
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
}

.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid black;
}

#add-comment-form {
  border-bottom: 2px solid black;
  padding: 16px;
}

#add-comment-form label{
  width: 100%;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}

#add-comment-form input, textarea {
  display: block;
  width: 100%;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: 100%;
}

#upvotes-section button, p {
  display: inline-block;
  margin-right: 16px;
}

.comment {
  border-bottom: 2px solid black;
  padding: 16px;
}

.profile img{
  height: 35px;
  width: 35px;
}

.profile {
  float: right;
  display: block;
  padding-top: 16px;
}

/* Everything but the jumbotron gets side spacing for mobile first views */
.header,
.marketing,
.footer {
  padding-left: 15px;
  padding-right: 15px;
}

/* Custom page header */
.header {
  border-bottom: none;
  display: inline-block;
  background-color: lightsteelblue;
  height: 75px;

}

.floatright{
  float: right;
  right: initial;
  margin-right: 0 ;
}

.nav-pane {
  display: inline-block;
  width: 31.3%;
  height: 100%;
  float: left;
}

/* Make the masthead heading the same height as the navigation */
.header h3 {
  margin-top: 0;
  margin-bottom: 0;
  line-height: 40px;
  padding-bottom: 19px;
}

.center {
  text-align: center;
}

/*dropdown*/

/* The container <div> - needed to position the dropdown content */
.menu {
  position: relative;
  display: block;
  margin-top: 25px;
}

/* Dropdown Content (Hidden by Default) */
.dropdown-list {
  display: none;
  position: absolute;
  background-color: lightsteelblue;
  min-width: 160px;
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  z-index: 1;
}

/* Links inside the dropdown */
.dropdown-list a {
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

/* Change color of dropdown links on hover */
.dropdown-list a:hover {
  background-color: white;
  color: lightsteelblue;
}

/* Show the dropdown menu (use JS to add this class to the .dropdown-content container when the user clicks on the dropdown button) */
.show {display:block;}

.menu button{
  border: none;
  margin: 0;
  text-decoration: none;
  background: none;
  color: white;
  font-family: sans-serif;
  font-size: 1rem;
  cursor: pointer;
  text-align: center;
  transition: background 250ms ease-in-out,
  transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
  background: transparent;
}